.auth-mobile {
  margin-top: 56px;
}

.auth-form {
  margin: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-materialComponent {
  &.auth-input {
    width: 100%;
    margin: 12px 0;
  }
  &.auth-button {
    margin-top: 22px;
    .MuiButton-label {
      padding-top: 2.5px;
      padding-bottom: 2.5px;
    }
  }
}

.auth-desktop,
.auth-buttonSecondary.auth-materialComponent {
  display: none;
}

@media (min-width: 600px) {
  .auth-mobile {
    margin-top: 64px;
  }
}

@media (min-width: 992px) {
  .auth-mobile,
  .auth-buttonPrimary.auth-materialComponent {
    display: none;
  }
  .auth-buttonSecondary.auth-materialComponent {
    display: flex;
  }
  .auth-desktop {
    margin: 254px 140px 180px 140px;
    display: flex;
    justify-content: space-between;
  }
  .auth-loginPanel,
  .auth-registerPanel {
    min-width: 400px;
    margin: 0 30px;
    h3 {
      text-align: center;
      font-size: 1.4rem;
      font-weight: bold;
    }
  }
}
