@import "../../../styles/variables";

.startPage {
  margin-top: 56px;
}

.startPage-buttonLabel {
  padding-right: 8px;
  font-size: 0.875rem;
}

.startPage-button.startPage-materialComponent {
  border: 1.5px solid white;
  border-radius: 5px;
  color: white;
  z-index: 2;
}

.startPage-imageWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 40vh;
  background-position: center;
  margin-bottom: 10px;
}

.startPage-imageWrapper::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  display: block;
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
}

.startPage-WrapperContent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.startPage h2 {
  z-index: 2;
  margin-top: 90px;
  margin-bottom: 20px;
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
  font-weight: bolder;
  color: #fff;
}

.startPage-imageMan {
  background-image: url("../../../assets/ana-maria-nichita-hp7Uj0EO__g-unsplash.jpg");
}

.startPage-imageWoman {
  background-image: url("../../../assets/nikhil-uttam-cEPOwfSYm4Q-unsplash.jpg");
}

.startPage-imageKids {
  background-image: url("../../../assets/annie-spratt-ja_hJ4uG-JU-unsplash.jpg");
}

@media (min-width: 512px) {
  .startPage-imageWrapper {
    height: auto;
    padding-top: 30%;
    padding-bottom: 30%;
  }
}

@media (min-width: 992px) {
  .startPage {
    margin-top: 154px;
    h2 {
      font-size: 2.75rem;
      letter-spacing: 0.175rem;
    }
  }
  .startPage-buttonLabel {
    font-size: 1.125rem;
  }
}
